@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.5rem;
	background-color: var(--mainBackground);
	color: white;
}

.header__textDecoration {
	text-decoration: none;
}

.header__logo {
	font-size: 40px;
	color: #ececec;
	text-shadow: 0 0 25px rgba(255, 255, 255, 0.178);
	cursor: pointer;
}

.header__menu {
	display: flex;
	justify-content: space-around;
	align-items: center;
	list-style: none;
	font-size: 18px;
}

.header__link > * {
	margin: 0 0.6rem;
	color: var(--text);
	font-size: 20px;
}

.header__linkText:hover {
	color: white;
	text-shadow: 0 0 25px rgba(255, 255, 255, 0.288);
}

.header__mobile__item:hover {
	background-color: #333333;
}

@media only screen and (max-width: 768px) {
	.header__logo {
		font-size: 32px;
	}
}

.popup__link {
	color: white;
}

.popup__link:hover {
	border-bottom: 2px solid whitesmoke;
	cursor: pointer;
}

.portfolio__container {
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	width: 80%;
}

.portfolio__container > * {
	margin: 2rem 0;
}

.portfolio__intro__container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 1rem;
	min-height: 40vh;
	background-color: var(--portfolioIntroBackground);
}

.portfolio__intro__container > * {
	margin: 1rem 0;
}

.portfolio__intro__title {
	font-size: 50px;
	color: white;
}

/* Portfolio Grid Layout */
.portfolio__main__container {
	text-align: center;
}

.portfolio__main__container > * {
	margin: 1.5rem 0;
}

.portfolio__main__title {
	font-size: 32px;
	color: rgba(245, 245, 245, 0.856);
}

.portfolio__grid__title {
	padding: 0.5rem;
	color: rgba(245, 245, 245, 0.719);
	font-size: 24px;
	letter-spacing: 1px;
	background-color: #21252c;
	border-radius: 15px 15px 0 0;
}

.portfolio__grid__imageContainer {
	position: relative;
}

.portfolio__grid__image {
	max-height: 100%;
	max-width: 100%;
	border-radius: 0 0 15px 15px;
}

.portfolio__grid__image:hover {
	background-color: #4065a7;
}

.portfolio__grid__item:hover .portfolio__grid__overlay {
	display: block;
	background-color: #21252cbb;
}

/* Portfolio grid item overlay section */
.portfolio__grid__overlay {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: transparent;
	border-radius: 0 0 15px 15px;
	cursor: default;
	z-index: 3;
	padding: 0.5rem;
	display: none;
}

.portfolio__grid__overlay__items {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	height: 100%;
}

.portfolio__grid__text {
	text-align: left;
	color: whitesmoke;
}

.portfolio__grid__github {
	color: #e9e9e9;
}

.portfolio__grid__github:hover {
	/* color: #026ccf; */
	/* color: #4183c4; */
	color: #6b7280;
	/* border-radius: 50px;
	background-color: #a7a7a736; */
}

