/*} */

.bi-github:hover {
	color: #959ca7;
}

.bi-resume:hover {
	color: #6577e0;
}

.bi-linkedin:hover {
	color: #4183c4;
}

.contact__title {
  font-size: 32px;
  border-bottom: 2px solid grey;
  border-radius: 1px;
  width: 110px;
  margin: 0 auto .5rem auto;
}

.contact__btn__container {
  text-decoration: none;
  display: flex;
  justify-content: center;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:focus {
  outline: none;
}

:root {
  --mainBackground: #0F1217;
  --mainChildrenBackground: #0E1A36;
  /* --headerBackground: #24315E;
  --portfolioIntroBackground: #303C6C;
  --portfolioGridBackground: #439684;
  --contactFormBackground: #303C6C; */
  --text: #fcfcfcbb;
  --btn: #4978e7;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  font-family: Roboto;
  background-color: #0F1217;
  height: 100vh;
  overflow-y: auto;
}

.contact__container {
  max-width: 50%;
  margin: 7rem auto;
  padding: 1.5rem;
  background-color: "#e9e9e9";
}

@media only screen and (max-width: 1168px) {
  .contact__container {
    max-width: 65%;
  }
}

@media only screen and (max-width: 1068px) {
  .contact__container {
    max-width: 80%;
  }
}
